import { ITodo } from '../todo/model';
import { useState } from 'react';
import { Button, Input, Box, ButtonGroup } from '@mui/joy';
import { Checkbox } from '@mui/material';

interface TodoProps {
  todo: ITodo;
  deleteTodo: () => void;
  statusTodo: () => void;
}

function Todo({ todo, deleteTodo, statusTodo }: TodoProps) {
  const [edit, setEdit] = useState(false);
  const [value, setValue] = useState('');

  function editTodo(title: string) {
    setEdit(true);
    setValue(title);
  }

  function changeTodo(event: React.ChangeEvent<HTMLInputElement>) {
    setValue(event.target.value);
  }

  function saveTodo() {
    todo.title = value;
    setEdit(false);
  }

  function canselTodo() {
    setEdit(false);
  }

  return (
    <Box>
      {edit ? (
        <Box display='flex' justifyContent='left' margin='5px 0'>
          <Input value={value} onChange={changeTodo} size='sm' variant='soft' color='success' />
          <ButtonGroup size='sm' spacing={1} sx={{ marginLeft: '5px' }} color='success'>
            <Button onClick={saveTodo}>Save</Button>
            <Button onClick={canselTodo}>Cancel</Button>
          </ButtonGroup>
        </Box>
      ) : (
        <Box>
          <Checkbox color='success' onChange={statusTodo} checked={todo.completed} />
          <span style={{ fontSize: '20px', marginLeft: '5px' }}>{todo.title}</span>
          <ButtonGroup size='sm' spacing={1}>
            <Button onClick={() => editTodo(todo.title)} color='success'>
              Edit
            </Button>
            <Button onClick={deleteTodo} color='warning'>
              Delete
            </Button>
          </ButtonGroup>
        </Box>
      )}
    </Box>
  );
}

export default Todo;
