import { ITodo } from '../todo/model';
import { Button, ToggleButtonGroup } from '@mui/joy';
import React from 'react'

interface TodoFilterProps {
  todos: ITodo[];
	setFiltered: (arg: ITodo[]) => void
}

function TodoFilter({ todos, setFiltered }: TodoFilterProps) { 
	const [value, setValue] = React.useState<string | null>('default')

	function todoFilter(status: 'all' | boolean ) {
		if(status === 'all') {
			setFiltered(todos)
		} else {
			let newTodos = [...todos].filter(todo => todo.completed === status)
			setFiltered(newTodos)
		}
	}
	
	

  return (

		<ToggleButtonGroup 
		sx={{margin: '20px 0',  justifyContent: 'center'}}  
		size='sm'
		spacing={1} 
		color='success'
		value={value}
		onChange={(event, newValue) => {
			setValue(newValue)
		}}
		>
			<Button value="default" onClick={() => todoFilter('all')}>Show All Tasks</Button>
      <Button value="active" onClick={() => todoFilter(false)}>Show Active Tasks</Button>
      <Button value="completed"  onClick={() => todoFilter(true)}>Show completed Tasks</Button>
		</ToggleButtonGroup>

  );
}
export default TodoFilter;
